import { deleteApplicant, getApplicant, updateApplicant } from "../Service/api";
import { useState, useEffect, useContext } from "react";
import "bootstrap/dist/js/bootstrap";
import { useNavigate } from "react-router-dom";
import { ApplicantContext } from "../App";

const ViewApplicant = () => {
  const [applicantsData, setApplicantsData] = useState([]);
  const navigate = useNavigate();
  const [applicantData, setApplicantData] = useState({
    studentName: "",
    registrationNumber: "",
  });

  const { studentName, registrationNumber, img } = applicantData;
  const [id, setId] = useState("");
  const [reload, setReload] = useState(0);
  const [applicant, setApplicant] = useContext(ApplicantContext);

  const handleChannge = (e) => {
    setApplicantData({ ...applicantData, [e.target.name]: e.target.value });
  };

  useEffect(() => {
    getApplicatsDetails();
  }, [reload]);

  const getApplicatsDetails = async () => {
    const result = await getApplicant();
    setApplicantsData(result.data);
  };

  const handleDeleteApplicant = async (id) => {
    const result = await deleteApplicant(id);
    setReload(reload + 1);
  };

  const handleUpdateApplicant = async (id, applicant) => {
    const result = await updateApplicant(id, applicant);
    // console.log(result);
    setReload(reload + 1);
  };

  return (
    <div>
      <div className="col-md-6 position-absolute start-50 translate-middle-x mt-5">
        <table className="table">
          <thead>
            <tr>
              <th scope="col">Student Name</th>
              <th scope="col">Registration Number</th>
            </tr>
          </thead>
          <tbody>
            {applicantsData.map((details, i) => (
              <tr key={i}>
                <td>{details.studentName}</td>
                <td>{details.registrationNumber}</td>
                <td>
                  <button
                    className="btn btn-warning"
                    data-bs-toggle="modal"
                    data-bs-target="#staticBackdrop"
                    onClick={() => {
                      const { _id, ...data } = details;
                      setApplicantData({ ...data });
                      setId(_id);
                    }}
                  >
                    Edit
                  </button>
                </td>
                <td>
                  <button
                    onClick={() => handleDeleteApplicant(details._id)}
                    className="btn btn-danger"
                  >
                    Delete
                  </button>
                </td>
                <td>
                  <button
                    onClick={() => {
                      setApplicant(details);
                      navigate("/boardingPass");
                    }}
                    className="btn btn-danger"
                  >
                    Boarding&nbsp;Pass
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div
        className="modal fade"
        id="staticBackdrop"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabindex="-1"
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="staticBackdropLabel">
                Modal title
              </h5>
            </div>
            <div className="modal-body">
              <form>
                <label className="mb-2">Student Name</label>
                <input
                  type="text"
                  className="form-control mb-3"
                  name="studentName"
                  value={studentName}
                  onChange={(e) => handleChannge(e)}
                />

                <label className="mb-2">Registration Number</label>
                <input
                  type="text"
                  className="form-control mb-3"
                  name="registrationNumber"
                  value={registrationNumber}
                  onChange={(e) => handleChannge(e)}
                />
              </form>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => handleUpdateApplicant(id, applicantData)}
                data-bs-dismiss="modal"
              >
                Save Changes
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ViewApplicant;
