import React, { useContext } from "react";
import { ApplicantContext } from "../App";
import styled from "styled-components";

function BoardingPass(props) {
  const [applicant, setApplicant] = useContext(ApplicantContext);
  return (
    <Container style={{ width: "100vw" }}>
      <ImageContainer>
        <div className="Image">
          {applicant.img && <img src={applicant.img} alt="" />}
        </div>
      </ImageContainer>
      <Details>
        <h2 className="mb-2">Student Name</h2>
        <p>{applicant.studentName}</p>

        <h3 className="mb-2">Registration Number</h3>
        <p>{applicant.registrationNumber}</p>
        <h3 className="mb-2">Gender</h3>
        <p>{applicant.gender}</p>
        <h3 className="mb-2">Preference</h3>
        <p>{applicant.preference}</p>
      </Details>
    </Container>
  );
}

export default BoardingPass;
const Container = styled.div`
  display: flex;
  height: 100vh;
  padding: 2rem;
  width: 100%;
  justify-content: center;
  transition: all linear 2s;

  @media (max-width: 500px) {
    flex-direction: column;
    padding: 1rem;
  }
`;
const ImageContainer = styled.div`
  .Image {
    height: 45rem;
    border: none;
  }
  @media (max-width: 500px) {
    .Image {
      height: 25rem;
      border: none;
    }
  }
`;
const Details = styled.div`
  padding: 4rem 2rem;
  h2 {
    font-size: 3rem;
    color: blsck;
  }
  h3 {
    color: blsck;
    font-size: 1.4rem;
  }
  p {
    font-size: 1.5rem;
  }
  @media (max-width: 500px) {
    padding: 1rem 0rem;
    h2 {
      font-size: 1.5rem;
    }
    p {
      font-size: 1rem;
    }
  }
`;
const Buttons = styled.div`
  display: flex;
  justify-content: space-between;
  color: white;
`;
