import { useRef, useState } from "react";
import { addApplicant } from "../Service/api";
import { db, firebaseApp, storage } from "../firebase/config";
import "./AddData.css";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";

const AddApplicant = () => {
  const [applicantData, setApplicantData] = useState({
    studentName: "",
    registrationNumber: "",
  });

  const [image, setImage] = useState("");

  const [preference, setPreference] = useState("");
  const [gender, setGender] = useState("");
  const hiddenFileInput = useRef("");

  const { studentName, registrationNumber } = applicantData;
  const [error, setError] = useState(false);
  const handleChannge = (e) => {
    setApplicantData({ ...applicantData, [e.target.name]: e.target.value });
  };

  const UploadImage = async (ref) => {
    return await uploadBytes(ref, image).then(() => {
      return getDownloadURL(ref);
    });
  };

  const handleClick = (e) => {
    e.preventDefault();
    hiddenFileInput.current.click();
  };

  const addDetails = async (e) => {
    e.preventDefault();
    // alert(JSON.stringify(data));
    const sref = ref(
      storage,
      `Applicant/Images/${applicantData.registrationNumber}`
    );
    UploadImage(sref).then(async (url) => {
      const data = { ...applicantData, preference, gender, img: url };
      try {
        await addApplicant(data);
        setError(false);
      } catch (error) {
        setError(true);
      }
    });

    setApplicantData({ studentName: "", registrationNumber: "" });
    setImage("");
    setGender("");
    setPreference("");
  };

  return (
    <div className="d-flex justify-content-center">
      <div className="col-6 mt-5">
        <form>
          <label className="mb-2">Student Name</label>
          <input
            type="text"
            className="form-control mb-3"
            name="studentName"
            value={studentName}
            onChange={(e) => handleChannge(e)}
          />

          <label className="mb-2">Registration Number</label>
          <input
            type="text"
            className="form-control mb-3"
            name="registrationNumber"
            value={registrationNumber}
            onChange={(e) => handleChannge(e)}
          />
          <select
            className="p-2 bg-warning text-black mb-3 "
            style={{ fontWeight: "bold", border: "none" }}
            onChange={(e) => setPreference(e.target.value)}
          >
            <option disabled selected>
              Hostel Preferences
            </option>
            <option>Johar Hall</option>
            <option>MA Jinnah Hall</option>
            <option>Jupiter Hall</option>
          </select>
          <select
            className="p-2 bg-warning text-black mb-3 ms-3 "
            style={{ fontWeight: "bold", border: "none" }}
            onChange={(e) => setGender(e.target.value)}
          >
            <option disabled selected>
              Gender
            </option>
            <option>Male</option>
            <option>Female</option>
          </select>
          <div>
            <h5>Profile Picture</h5>
            <div className="Image">
              {image && <img src={URL.createObjectURL(image)} alt="" />}
            </div>
            <button
              style={{
                display: "block",
                fontSize: "1rem",
                padding: "1rem",
                marginBlock: "1.2rem",
                fontWeight: "bold",
              }}
              className="btn btn-warning"
              onClick={handleClick}
            >
              Upload Image
            </button>
            <input
              type="file"
              ref={hiddenFileInput}
              onChange={(e) => {
                if (e.target.files[0]) {
                  setImage(e.target.files[0]);
                }
              }}
              style={{ display: "none" }}
              accept={".jpg, .png, .jpeg"}
            />
          </div>
          <button
            className="btn btn-warning form-control p-2 fw-bold "
            onClick={(e) => addDetails(e)}
          >
            Apply
          </button>
          {error && <p className="text-danger">Something is wrong</p>}
        </form>
      </div>
    </div>
  );
};

export default AddApplicant;
