import Navbar from "./components/Navbar";
import { Routes, Route } from "react-router-dom";
import AddApplicant from "./components/AddApplicant";
import ViewApplicant from "./components/ViewApplicant";
import UpdateRecord from "./components/UpdateRecord";
import DeleteRecord from "./components/DeleteRecord";
import BoardingPass from "./components/BoardingPass";
import { createContext, useState } from "react";
export const ApplicantContext = createContext();

function App() {
  const [applicant, setApplicant] = useState({
    studentName: "",
    registrationNumber: "",
    gender: "",
    preference: "",
    img: "",
  });
  return (
    <ApplicantContext.Provider value={[applicant, setApplicant]}>
      <div>
        <h1 className="text-center">Hostel Application System</h1>
        <Navbar />
        <Routes>
          <Route path="/hostelApplicants" element={<AddApplicant />} />
          <Route path="/viewApplicants" element={<ViewApplicant />} />
          <Route path="/updateRecord" element={<UpdateRecord />} />
          <Route path="/deleteRecord" element={<DeleteRecord />} />
          <Route path="/boardingPass" element={<BoardingPass />} />
        </Routes>
      </div>
    </ApplicantContext.Provider>
  );
}

export default App;
